<template>
  <section class="claims-on-line">
      <div class="lc-tips">
        <span>申请前-准备理赔审请材料</span>
      </div>
      <ul>
          <li class="">
              ① 与投保人一致的身份证件
          </li>
          <li class="">
              ② 被投保人本人或监护人银行卡
          </li>
          <li class="">
              ③ 住院病历/出院小结/住院检查报告
          </li>
          <li class="">
              ④ 医疗费用发票
          </li>
          <li class="">
              ⑤ 费用明细清单
          </li>
          <li class="">
              ⑥ 意外事故证明
          </li>
          <li class="">
              ⑦ 病例
          </li>
          <li class="">
              ⑧ 诊断证明（如有请准备）
          </li>
          <li class="">
              ⑨ 其他材料
          </li>
      </ul>
      <div class="text">理赔所需材料，以在线理赔中为准。</div>
      <div class="btn-wrap-new">
        <button class="submit-btn-1" @click="jump">下一步</button>
      </div>
  </section>
</template>

<script>
export default {
    data () {
        return {
            isShow: true,
            activation: null
        };
    },
    created () {
    },
    methods: {
        jump () {
            // claimStatus -10可以理赔 10不可理赔
            if (Number(this.$route.query.claimStatus) === -10) {
                this.$router.push({name: 'information', query: this.$route.query});
            } else {
                this.$mint.Toast({
                    message: '您已申请过理赔',
                    position: 'top',
                    duration: 1000
                });
            }
        }
    }
};
</script>
<style lang="scss" scoped>
  .text {
    margin-top: 20px;
    padding-left: 0.65rem;
  }
</style>



