<template>
  <section class="noticeInsure policy-serve">
    <mt-loadmore v-if="list && list.length > 0" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
      <ul>
          <!-- -10投保失败，10审核中，20保障中 -->
          <li v-for="(item, index) in list" :key="index">
              <div class="wrap" @click="goto(item)">
                  <div class="item t">
                    <div class="title">{{item.insuranceProductName}}</div>
                  </div>
                  <div class="item row">
                    <div class="col-sm col-sm-8 l">被保人：{{item.realName}}</div>
                    <div class="col-sm col-sm-4 r active-1" v-if="item.status === 20">{{item.status | policyStatus}}</div>
                    <div class="col-sm col-sm-4 r active-2" v-if="item.status === 10">{{item.status | policyStatus}}</div>
                    <div class="col-sm col-sm-4 r active-4" v-if="item.status === -10">{{item.status | policyStatus}}</div>
                    <div class="col-sm col-sm-4 r active-3" v-if="item.status === -20">{{item.status | policyStatus}}</div>
                  </div>
                  <div v-if="item.status === 20 || item.status === -20">
                    <div class="item">保障期限：{{item.periodStart | date}} 至 {{item.periodEnd | date}}</div>
                    <div class="item">保单号：{{item.insurancePersonalNo}}</div>
                  </div>
                  <!-- 10个人，20机构，30应用 -->
                  <div class="item" style="color: #72b81e">{{item.fromName}}</div>
                  <div class="item" v-if="item.status === -10">
                      <span class="error-text">失败说明：</span>
                      {{item.reason}}
                  </div>
              </div>
              <div class="b" v-if="item.isReInsurance" @click="jump(item)">续保</div>
          </li>
      </ul>
    </mt-loadmore>
  </section>
</template>

<script>
export default {
    data () {
        return {
            allLoaded: false,
            bottomText: '上拉加载更多...',
            list: [
                // {
                //     realName: '测试685',
                //     reason: '',
                //     insurancePersonalNo: '12321',
                //     insuranceProductId: 1,
                //     insuranceProductName: '中国志愿服务保障卡',
                //     from: 30,
                //     id: 2,
                //     cardNo: 310326198709275236,
                //     periodStart: '',
                //     isReInsurance: false,
                //     periodEnd: '',
                //     status: -20,
                //     fromName: '由志趣基金会捐赠'
                // }
            ],
            form: {
                personFirstAccount: null,
                pageSize: 10,
                pageNumber: 1
            }
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.form.personFirstAccount = pfa);
            this.$http.post(this.$api.insurance.queryInsurancePersonalByPersonFirstAccount, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        // 续保
        jump (item) {
            // 进入自然人保险产品详情
            this.$router.push({name: 'details', query: {id: item.id, insuranceProductId: item.insuranceProductId, type: 'xubao'}});
        },
        goto (item) {
            if (item.status === 20 || item.status === -20) {
                // 进入查看保险详情
                this.$router.push({name: 'insuranceDetails', query: {type: 'query', insuranceNo: item.insurancePersonalNo}});
            }
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.insurance.queryInsurancePersonalByPersonFirstAccount, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        }
    }
};
</script>



