<template>
  <section class="insure-my claims-serve">
    <ul>
        <li @click="jump(1)">
          <span class="row">
              <div class="col-sm col-sm-6 l">
                <span>
                  <i class="iconfont icon-cooperation"></i>
                </span>
                <em>我要理赔</em>
              </div>
              <div class="col-sm col-sm-6 r">
                <i class="iconfont icon-arrow-right"></i>
              </div>
          </span>
        </li>
        <li @click="jump(2)">
          <span class="row">
              <div class="col-sm col-sm-6 l">
                <span>
                  <i class="iconfont icon-loupe"></i>
                </span>
                <em>理赔查询</em>
              </div>
              <div class="col-sm col-sm-6 r">
                <i class="iconfont icon-arrow-right"></i>
              </div>
          </span>
        </li>
    </ul>
  </section>
</template>

<script>
export default {
    data () {
        return {
        };
    },
    created () {
    },
    methods: {
        jump (type) {
            type === 1 ? this.$router.push({name: 'myClaim'}) : this.$router.push({name: 'query'});
        }
    }
};
</script>
<style lang="scss" scoped>
  .claims-serve {
    li {
      .l {
        span {
          width: px2rem(25);
          height: px2rem(25);
          line-height: px2rem(25);
          display: inline-block;
          text-align: center;
          background-color: #73b81f;
          border-radius: 50%;
          margin-right: px2rem(4);
          .icon-cooperation:before, .icon-loupe:before {
            color: #fff;
            font-size: 14px;
          }
          .iconfont {
            vertical-align: none;
          }
        }
        em {
          vertical-align: middle;
        }
      }
    }
  }
</style>



