              </div>
            </div>
            <div class="c" v-if="form.costImgs.length < 10">
              <label for="file4" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file4" ref="inputer4" accept="image/png, image/jpeg, image/gif, image/jpg" class="inputfile" @change="uploadImg(4, form.costImgs)" />
            </div>
            <div class="b">已上传 <span>{{form.costImgs.length}}</span> 张照片，最多上传 <span>10</span> 张照片</div>
          </li>
          <li>
            <div class="t row">
              <div class="col-sm col-sm-6 l">药方（如配药必传） </div>
              <!-- <div class="col-sm col-sm-6 r">查看样张</div> -->
            </div>
            <div class="block" v-if="form.prescriptionImgs.length > 0">
              <div class="img-box" v-for="(item, index) in form.prescriptionImgs" :key="index">
                <img :src="item" alt="" @click="showImg(item)">
                <i class="iconfont icon-close-blod" @click="dele(index, form.prescriptionImgs)"></i>
              </div>
            </div>
            <div class="c" v-if="form.prescriptionImgs.length < 10">
              <label for="file5" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file5" ref="inputer5" accept="image/png, image/jpeg, image/gif, image/jpg" class="inputfile" @change="uploadImg(5, form.prescriptionImgs)" />
            </div>
            <div class="b">已上传 <span>{{form.prescriptionImgs.length}}</span> 张照片，最多上传 <span>10</span> 张照片</div>
          </li>
          <li>
            <div class="t row">
              <div class="col-sm col-sm-9 l">门诊检查报告<span class="small">（如有请上传）</span> </div>
              <!-- <div class="col-sm col-sm-3 r">查看样张</div> -->
            </div>
            <div class="block" v-if="form.outpatientDepartmentImgs.length > 0">
              <div class="img-box" v-for="(item, index) in form.outpatientDepartmentImgs" :key="index">
                <img :src="item" alt="" @click="showImg(item)">
                <i class="iconfont icon-close-blod" @click="dele(index, form.outpatientDepartmentImgs)"></i>
              </div>
            </div>
            <div class="c" v-if="form.outpatientDepartmentImgs.length < 10">
              <label for="file6" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file6" ref="inputer6" accept="image/png, image/jpeg, image/gif, image/jpg" class="inputfile" @change="uploadImg(6, form.outpatientDepartmentImgs)" />
            </div>
            <div class="b">已上传 <span>{{form.outpatientDepartmentImgs.length}}</span> 张照片，最多上传 <span>10</span> 张照片</div>
          </li>
          <li>
            <div class="t row">
              <div class="col-sm col-sm-9 l">银行卡正反面（必传）<span class="small"></span> </div>
            </div>
            <div class="block" v-if="form.bankCardImgs.length > 0">
              <div class="img-box" v-for="(item, index) in form.bankCardImgs" :key="index">
                <img :src="item" alt="" @click="showImg(item)">
                <i class="iconfont icon-close-blod" @click="dele(index, form.bankCardImgs)"></i>
              </div>
            </div>
            <div class="c" v-if="form.bankCardImgs.length < 2">
              <label for="file7" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file7" ref="inputer7" accept="image/png, image/jpeg, image/gif, image/jpg" class="inputfile" @change="uploadImg(7, form.bankCardImgs)" />
            </div>
            <div class="b">已上传 <span>{{form.bankCardImgs.length}}</span> 张照片，必须上传 <span>2</span> 张照片</div>
          </li>
          <li>
            <div class="t row">
              <div class="col-sm col-sm-9 l">入院证明/出院小结（如住院必传）<span class="small"></span> </div>
              <!-- <div class="col-sm col-sm-3 r">查看样张</div> -->
            </div>
            <div class="block" v-if="form.inOutHospitalImgs.length > 0">
              <div class="img-box" v-for="(item, index) in form.inOutHospitalImgs" :key="index">
                <img :src="item" alt="" @click="showImg(item)">
                <i class="iconfont icon-close-blod" @click="dele(index, form.inOutHospitalImgs)"></i>
              </div>
            </div>
            <div class="c" v-if="form.inOutHospitalImgs.length < 10">
              <label for="file8" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file8" ref="inputer8" accept="image/png, image/jpeg, image/gif, image/jpg" class="inputfile" @change="uploadImg(8, form.inOutHospitalImgs)" />
            </div>
            <div class="b">已上传 <span>{{form.inOutHospitalImgs.length}}</span> 张照片，最多上传 <span>10</span> 张照片</div>
          </li>
          <li>
            <div class="t row">
              <div class="col-sm col-sm-9 l">出院费用明细（如住院必传）<span class="small"></span> </div>
              <!-- <div class="col-sm col-sm-3 r">查看样张</div> -->
            </div>
            <div class="block" v-if="form.outCostListImgs.length > 0">
              <div class="img-box" v-for="(item, index) in form.outCostListImgs" :key="index">
                <img :src="item" alt="" @click="showImg(item)">
                <i class="iconfont icon-close-blod" @click="dele(index, form.outCostListImgs)"></i>
              </div>
            </div>
            <div class="c" v-if="form.outCostListImgs.length < 10">
              <label for="file9" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file9" ref="inputer9" accept="image/png, image/jpeg, image/gif, image/jpg" class="inputfile" @change="uploadImg(9, form.outCostListImgs)" />
            </div>
            <div class="b">已上传 <span>{{form.outCostListImgs.length}}</span> 张照片，最多上传 <span>50</span> 张照片</div>
          </li>
          <li>
            <div class="t row">
              <div class="col-sm col-sm-9 l">病理、血液、影像报告<span class="small"></span> </div>
              <!-- <div class="col-sm col-sm-3 r">查看样张</div> -->
            </div>
            <div class="block" v-if="form.caseReportImgs.length > 0">
              <div class="img-box" v-for="(item, index) in form.caseReportImgs" :key="index">
                <img :src="item" alt="" @click="showImg(item)">
                <i class="iconfont icon-close-blod" @click="dele(index, form.caseReportImgs)"></i>
              </div>
            </div>
            <div class="c" v-if="form.caseReportImgs.length < 10">
              <label for="file10" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file10" ref="inputer10" accept="image/png, image/jpeg, image/gif, image/jpg" class="inputfile" @change="uploadImg(10, form.caseReportImgs)" />
            </div>
            <div class="b">已上传 <span>{{form.caseReportImgs.length}}</span> 张照片，最多上传 <span>10</span> 张照片</div>
          </li>
          <li>
            <div class="t row">
              <div class="col-sm col-sm-9 l">转院证明（如转院必传）<span class="small"></span> </div>
              <!-- <div class="col-sm col-sm-3 r">查看样张</div> -->
            </div>
            <div class="block" v-if="form.turnHospitalImgs.length > 0">
              <div class="img-box" v-for="(item, index) in form.turnHospitalImgs" :key="index">
                <img :src="item" alt="" @click="showImg(item)">
                <i class="iconfont icon-close-blod" @click="dele(index, form.turnHospitalImgs)"></i>
              </div>
            </div>
            <div class="c" v-if="form.turnHospitalImgs.length < 10">
              <label for="file11" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file11" ref="inputer11" accept="image/png, image/jpeg, image/gif, image/jpg" class="inputfile" @change="uploadImg(11, form.turnHospitalImgs)" />
            </div>
            <div class="b">已上传 <span>{{form.turnHospitalImgs.length}}</span> 张照片，最多上传 <span>10</span> 张照片</div>
          </li>
      </ul>
      <div class="yes">
          <i class="iconfont icon-round-tick"></i>
          <span>我已确认</span>
      </div>
      <div class="button-wrap-new">
        <button class="submit-btn-1" @click="submit">提交</button>
      </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            fil: null,
            form: {
                cardNoImgs: [], // 身份证
                accidentImgs: [], // 事故证明
                emergencyImgs: [], // 急诊单
                costImgs: [], // 医疗费用原始凭证
                prescriptionImgs: [], // 药方
                outpatientDepartmentImgs: [], // 门诊检查报告
                bankCardImgs: [], // 银行卡
                inOutHospitalImgs: [], // 入院出院证明
                outCostListImgs: [], // 出院费用明细
                caseReportImgs: [], // 病理、血液、影像报告
                turnHospitalImgs: [] // 转院证明
            },
            query: {}
        };
    },
    created () {
        this.query = this.$route.query;
    },
    methods: {
        showImg (url) {
            this.$mint.MessageBox({
                title: '预览图片',
                message: `<img src="${url}">`
            });
        },
        uploadImg (type, list) {
            let inputDOM;
            if (type === 1) {
                inputDOM = this.$refs.inputer1;
            }
            if (type === 2) {
                inputDOM = this.$refs.inputer2;
            }
            if (type === 3) {
                inputDOM = this.$refs.inputer3;
            }
            if (type === 4) {
                inputDOM = this.$refs.inputer4;
            }
            if (type === 5) {
                inputDOM = this.$refs.inputer5;
            }
            if (type === 6) {
                inputDOM = this.$refs.inputer6;
            }
            if (type === 7) {
                inputDOM = this.$refs.inputer7;
            }
            if (type === 8) {
                inputDOM = this.$refs.inputer8;
            }
            if (type === 9) {
                inputDOM = this.$refs.inputer9;
            }
            if (type === 10) {
                inputDOM = this.$refs.inputer10;
            }
            if (type === 11) {
                inputDOM = this.$refs.inputer11;
            }
            // 通过DOM取文件数据
            this.fil = inputDOM.files;
            let formData = new FormData();
            let headers = {
                'Content-Type': 'multipart/form-data'
            };
            formData.append('file', this.fil[0]);
            formData.append('storage', 'platform-mgmt');
            formData.append('fileName', this.fil[0].name);
            this.$http.post('http://news.shcvs.cn/cm/upload/uploadImgFolderOSS?folderName=insurance', formData, headers).then(res => {
                if (res.data.errorcode === 0) {
                    list.push(res.data.data);
                }
            }).catch((e) => {
                alert(e);
            });
        },
        dele (idx, list) {
            list.splice(idx, 1);
        },
        submit () {
            if (!this.check()) return false;
            let params = {
                cardNoImgs: '',
                accidentImgs: '',
                emergencyImgs: '',
                costImgs: '',
                prescriptionImgs: '',
                outpatientDepartmentImgs: '',
                bankCardImgs: '',
                inOutHospitalImgs: '',
                outCostListImgs: '',
                caseReportImgs: '',
                turnHospitalImgs: ''
            };
            for (let i in this.form) {
                params[i] = this.form[i].join(';');
            }
            Object.assign(this.query, params);
            this.$http.post(this.$api.insurance.insurancePersonClaim, this.query).then(res => {
                if (res.data.code === 0) {
                    this.$router.push({name: 'result'});
                }
            });
        },
        check () {
            if (this.form.cardNoImgs.length < 2) {
                this.$mint.Toast({
                    message: '请上传身份证正反面',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.accidentImgs.length === 0) {
                this.$mint.Toast({
                    message: '请上传事故证明',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.emergencyImgs.length === 0) {
                this.$mint.Toast({
                    message: '请上传急诊单/病例证明',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.costImgs.length === 0) {
                this.$mint.Toast({
                    message: '请上传医疗费用原始凭证、收据',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.bankCardImgs.length < 2) {
                this.$mint.Toast({
                    message: '请上传银行卡正反面',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            return true;
        }
    }
};
</script>
<style lang="scss" scoped>
  .insure-upload {
    .inputfile {
      position:absolute;
      clip:rect(0 0 0 0);
    }
    li {
      .block {
        margin-top: px2rem(15);
        .img-box {
          position: relative;
          display: inline-block;
          img {
            width: px2rem(60);
            height: px2rem(60);
            display: inline-block;
            margin-right: 5px;
            margin-bottom: 15px;
          }
          i {
            position: absolute;
            top: -2px;
            right: 7px;
          }
          .icon-close-blod:before {
            color: red;
          }
        }
      }
    }
  }
</style>



