<template>
  <section class="policy-serve claims-my">
    <div v-if="list && list.length > 0">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
        <div class="lc-tips">
          <span>请选择需要理赔的保单</span>
        </div>
        <ul>
          <!-- 保障中 active-1 -->
            <li @click="jump" v-for="(item, index) in list" :key="index">
                <div class="wrap">
                    <div class="item t row">
                      <div class="col-sm col-sm-8 l title">{{item.insuranceProductName}}</div>
                      <div class="col-sm col-sm-4 r"></div>
                    </div>
                    <div class="item row">
                      <div class="col-sm col-sm-8 l">被保人：{{item.realName}}</div>
                      <div class="col-sm col-sm-4 r active-1" v-if="item.status === 20">{{item.status | status}}</div>
                      <div class="col-sm col-sm-4 r active-2" v-if="item.status === 10">{{item.status | status}}</div>
                      <div class="col-sm col-sm-4 r active-4" v-if="item.status === -10">{{item.status | status}}</div>
                    </div>
                    <div class="item">保障期限：{{item.periodStart}} 至 {{item.periodEnd}}</div>
                </div>
            </li>
        </ul>
      </mt-loadmore>
    </div>
    <div v-else class="page-error">
      <div class="error">
          <img src="../../assets/images/license-error.png" alt="">
          <div>暂无理赔</div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
    data () {
        return {
            allLoaded: false,
            bottomText: '上拉加载更多...',
            form: {
                personFirstAccount: null,
                pageSize: 10,
                pageNumber: 1
            },
            list: []
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.form.personFirstAccount = pfa);
            this.$http.post(this.$api.insurance.queryInsurancePersonalByPersonFirstAccountInSafety, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        jump () {
            this.$router.push({name: 'beforeApplying'});
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.insurance.queryInsurancePersonalByPersonFirstAccountInSafety, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        }
    }
};
</script>



