<template>
  <section class="noticeInsure policy-serve claims-query">
    <div v-if="list && list.length > 0">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
        <ul>
            <li v-for="(item, index) in list" :key="index">
                <div class="wrap" @click="goto(item)">
                    <div class="item t row">
                      <div class="col-sm col-sm-8 l title">{{item.insuranceProductName}}</div>
                      <div class="col-sm col-sm-4 r active-1" v-if="item.status === 10">{{item.status | queryStatus}}</div>
                      <div class="col-sm col-sm-4 r active-2" v-if="item.status === 20">{{item.status | queryStatus}}</div>
                      <div class="col-sm col-sm-4 r active-3" v-if="item.status === -20">{{item.status | queryStatus}}</div>
                      <div class="col-sm col-sm-4 r active-4" v-if="item.status === -10">{{item.status | queryStatus}}</div>
                    </div>
                    <div class="item row">
                      <div class="col-sm col-sm-8 l">被保人：{{item.realName}}</div>
                      <div class="col-sm col-sm-4 r"><i class="iconfont icon-arrow-right"></i></div>
                    </div>
                    <div class="item row">
                      <div class="col-sm col-sm-11 l">保障期限：{{item.periodStart | date}} 至 {{item.periodEnd | date}}</div>
                      <div class="col-sm col-sm-1 r"></div>
                    </div>
                </div>
                <div class="b" v-if="item.status === -10" @click="jump(item)">补充材料</div>
            </li>
        </ul>
      </mt-loadmore>
    </div>
    <div v-else class="page-error">
      <div class="error">
          <img src="../../assets/images/license-error.png" alt="">
          <div>暂无理赔</div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
    data () {
        return {
            allLoaded: false,
            bottomText: '上拉加载更多...',
            form: {
                personFirstAccount: null,
                pageSize: 10,
                pageNumber: 1
            },
            list: []
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.form.personFirstAccount = pfa);
            this.$http.post(this.$api.insurance.queryClaimByPersonFirstAccount, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        // 保险详情
        goto (item) {
            this.$router.push({name: 'insuranceDetails', query: {insuranceNo: item.insuranceNo, type: 'query'}});
        },
        // 补充材料
        jump (item) {
            // this.$router.push({name: 'information', query: {insuranceNo: item.insuranceNo, type: 'query'}});
            item['type'] = 'query';
            this.$router.push({name: 'quickCompensation', query: item});
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.insurance.queryClaimByPersonFirstAccount, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        }
    }
};
</script>



