<template>
    <section class="mui-content insure-origin">
      <div class="search" v-if="list && list.length > 0">
        <input type="text" placeholder="请输入活动名称" v-model="search.searchValue">
        <i class="iconfont icon-loupe" @click="searchOder"></i>
      </div>
      <mt-loadmore v-if="list && list.length > 0" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
        <section class="list">
          <ul>
              <li v-for="(item, index) in list" :key="index" @click="goto(item)">
                <div class="item">
                    <div class="t">{{item.name}}</div>
                    <div>所属活动：{{item.title}}</div>
                    <div>时间：{{item.arriveTime}}</div>
                    <div>地点：{{item.address}}</div>
                </div>
              </li>
          </ul>
        </section>
      </mt-loadmore>
      <div class="error" v-else>
        <img src="../../assets/images/license-error.png" alt="">
        <p>暂无订单</p>
      </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            search: {
                searchValue: null
            },
            allLoaded: false,
            bottomText: '上拉加载更多...',
            form: {
                months: 6, // 近几个月
                user: null,
                pageSize: 10,
                pageNumber: 1
            },
            list: [],
            api: null
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.form.user = pfa);
            this.getList();
        },
        searchOder () {
            this.$http.post(this.$api.insurance.search, this.search).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        getList () {
            this.$http.post(this.$api.insurance.oder, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        goto (item) {
            // item['type'] = 'oder';
            localStorage.setItem('oderForm', JSON.stringify(item));
            this.$router.push({name: 'information', query: {type: 'click'}});
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.insurance.oder, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        }
    }
};
</script>
<style lang="scss" scoped>
  .search {
    padding: px2rem(15);
    position: relative;
    width: 100%;
    input {
      width: 100%;
      border-radius: 35px;
      padding-left: px2rem(10);
      height: px2rem(35);
      border: 1px solid #d5d5d5;
      padding-right: 28px;
      background-color: rgba(255, 255, 255, 0.3);
    }
    i {
      position: absolute;
      right: px2rem(25);
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .list {
    .item {
      color: #999;
      .t {
        font-size: 16px;
        color: #666;
      }
    }
    li:nth-child(1) {
      margin-top: 2px;
    }
  }
</style>



