<template>
    <section class="container-fluid">
        <router-view
          keep-alive
          transition
          transition-mode="out-in">
        </router-view>
    </section>
</template>

<script>
import axios from 'axios';
export default {
    data () {
        return {
            token: 'eyJhbGciOiJIUzI1NiJ9.eyJqdGkiOiIxMDAxNjU4Iiwic3ViIjoiMTAwMTY1ODs3Mi0wMDAwMDg4OS03OzE4NTAxMDcwMDAxIiwiaXNzIjoi5LiK5rW35biC5b-X6Laj5YWs55uK5Z-66YeR5LyaIiwiaWF0IjoxNTU5MjkyMDMxLCJleHAiOjE1NjA1ODgwMzF9.kxU_i_j2tcHEae_Vb3nMliMP66AzZ5itRg07EkoL-q8',
            userNo: '72-00000896-7'
        };
    },
    created () {
        // 本地
        // axios.defaults.headers.token = this.token;
        // localStorage.setItem('personFirstAccount', this.userNo);
        // localStorage.setItem('token', this.toke);
        // 线上
        let storage = localStorage.getItem('appUserInfo');
        if (storage) {
            storage = JSON.parse(storage);
            axios.defaults.headers.token = storage.cookieResp.token;
            localStorage.setItem('token', storage.cookieResp.token);
            localStorage.setItem('personFirstAccount', storage.userNo);
        }
    }
};
</script>

<style lang="scss">
    @import './styles/app';
</style>



