<template>
    <section class="mui-content insure-origin">
      <mt-loadmore v-if="list && list.length > 0" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
        <section class="list">
          <ul>
              <li v-for="(item, index) in list" :key="index" @click="goto(item.id)">
                <span class="row">
                  <div class="col-sm col-sm-2 l">
                      <img :src="item.imgs" alt="">
                    </div>
                    <div class="col-sm col-sm-9 c">
                      <div class="title">{{item.name}}</div>
                      <div>{{item.demo}}</div>
                    </div>
                    <div class="col-sm col-sm-1 r">
                      <i class="iconfont icon-arrow-right"></i>
                    </div>
                </span>
              </li>
          </ul>
          <!-- TODO 选购产品-应用 入口时应用 -->
          <!-- <div class="chat">
            <i class="iconfont icon-person-blod"></i>
          </div> -->
        </section>
      </mt-loadmore>
      <div class="error" v-else>
        <img src="../../assets/images/license-error.png" alt="">
        <p>暂无保险保障</p>
      </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            allLoaded: false,
            bottomText: '上拉加载更多...',
            form: {
                personFirstAccount: null,
                pageSize: 10,
                pageNumber: 1
            },
            list: [],
            api: null
        };
    },
    created () {
        this.form.personFirstAccount = this.$route.query.personFirstAccount;
        this.init();
    },
    methods: {
        init () {
            this.$http.post(this.$api.insurance.queryInsuranceProductByPersonRecommend, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        // 详情
        goto (id) {
            this.$router.push({name: 'details', query: {id: id}});
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.insurance.queryInsuranceProductByPersonRecommend, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        }
    }
};
</script>



