<template>
  <div class="wrap">
    <section class="securityPlan">
        <div class="listP">
            <ul>
                <li class="row">
                    <div class="col-sm col-sm-8 l">投保人年龄</div>
                    <div class="col-sm col-sm-4 r">{{form.insuranceMinAge}}-{{form.insuranceMaxAge}}岁</div>
                </li>
                <li class="row">
                    <div class="col-sm col-sm-4 l">保障范围</div>
                    <div class="col-sm col-sm-8 r">{{form.area}}</div>
                </li>
                <li class="row">
                    <div class="col-sm col-sm-4 l">医院范围</div>
                    <div class="col-sm col-sm-8 r">{{form.hospital}}</div>
                </li>
                <!-- <li class="row">
                    <div class="col-sm col-sm-4 l">报销范围</div>
                    <div class="col-sm col-sm-8 r">{{form.reimburse}}</div>
                </li> -->
                <li class="row">
                    <div class="col-sm col-sm-4 l">保障期限</div>
                    <div class="col-sm col-sm-8 r">
                      <span v-if="form.validityPeriod !== 0">{{form.validityPeriod}}年</span>
                      <span v-else>永久</span>
                    </div>
                </li>
                <li v-for="(item, index) in form.list" :key="index">
                    <div class="row top">
                      <div class="col-sm col-sm-8 l">{{item.name}}</div>
                      <div class="col-sm col-sm-4 r">{{item.amount | amount}}</div>
                    </div>
                    <div class="textarea" v-html="item.demo">{{item.demo}}</div>
                </li>
            </ul>
        </div>
      </section>
  </div>
</template>

<script>
export default {
    data () {
        return {
            form: {}
        };
    },
    created () {
        this.form = this.$route.query;
    },
    methods: {
    }
};
</script>
<style lang="scss" scoped>
  .securityPlan {
    width: 90%;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 5px;
    font-size: 14px;
    .listP {
      li {
        padding: px2rem(10) px2rem(15);
        border-bottom: 1px dashed #dcdcdc;
        .l {
          color: #666;
        }
        .r {
          text-align: right;
          color: #c6c6c6;
        }
      }
      li:nth-last-child(1) {
        border-bottom:none;
      }
    }
  }
  .wrap {
    height: 100%;
    padding-top: px2rem(12);
    background-color: #f2f2f2;
  }
  .textarea {
    width: 100%;
    margin-top: 10px;
    background-color: #fff;
    border-radius: 5px;
    border: 1px solid #ccc;
    color: #c6c6c6;
    // height: px2rem(250);
    padding: 5px;
  }
</style>



