<template>
    <section class="productDetail insuranceIntro">
        <!-- 自然人详情 -->
        <div v-if="!un" class="authentic row" :style="{backgroundImage: 'url('+note+')'}">
            <div class="col-sm col-sm-6 Tl"><i class="iconfont icon-attestation"></i><span>投保需实名认证</span></div>
        </div>
        <img class="headerImg" :src="form.imgs" alt="">
        <div class="safePlan">
            <div class="row J">
                <span class="col-sm col-sm-8 ">
                    <div class="lc-tip">
                        <span class="block"></span>
                        <span>保障计划</span>
                    </div>
                </span>
                <span class="col-sm col-sm-4 r" @click="goto(2)">查看详情<i class="iconfont icon-arrow-right"></i></span>
            </div>
            <div class="listL">
                <ul>
                    <li class="row" v-for="(item, index) in list" :key="index">
                        <div class="col-sm col-sm-6 Tl">{{item.name}}</div>
                        <div class="col-sm col-sm-6 Tr">{{item.amount | amount}}</div>
                    </li>
                    <li class="row">
                        <div class="col-sm col-sm-6 Tl">投保年龄</div>
                        <div class="col-sm col-sm-6 Tr">{{form.insuranceMinAge}}-{{form.insuranceMaxAge}}
                          <span v-if="form.reInsuranceAge !== 0">(可续保至{{form.reInsuranceAge}})</span>
                          <span v-else>(不可续保)</span>
                          </div>
                    </li>
                    <li class="row">
                        <div class="col-sm col-sm-6 Tl">保障期限</div>
                        <div class="col-sm col-sm-6 Tr">
                          <span v-if="form.validityPeriod === 0">永久</span>
                          <span v-else>{{form.validityPeriod}}年</span>
                        </div>
                    </li>
                    <li class="row">
                        <div class="col-sm col-sm-6 Tl">保费</div>
                        <div class="col-sm col-sm-6 Tr">{{form.insuranceAmount}}积分</div>
                    </li>
                </ul>
            </div>
        </div>
        <div class="safePlan">
            <div class="row J">
                <span class="col-sm col-sm-8 ">
                    <div class="lc-tip">
                        <span class="block"></span>
                        <span>理赔说明</span>
                    </div>
                </span>
                <span class="col-sm col-sm-4 r" @click="goto(1)">理赔须知<i class="iconfont icon-arrow-right"></i></span>
            </div>
            <div class="listL">
                <ul>
                    <li class="row">
                        <div class="Tl"><a :href="'tel:' + form.claimMobile">拨打理赔电话</a></div>
                    </li>
                    <li class="row">
                        <div class=" Tl">准备理赔材料</div>
                    </li>
                    <li class="row">
                        <div class=" Tl">完成理赔</div>
                    </li>
                </ul>
            </div>
        </div>
        <div class="safePlan">
            <div class="row J">
                <span class="col-sm col-sm-8 ">
                    <div class="lc-tip">
                        <span class="block"></span>
                        <span>被投保人信息</span>
                    </div>
                </span>
            </div>
            <div class="images">
                <div class="name"><span>{{user.realName}}</span></div>
                <div class="phone">
                    电话：<span>{{user.mobile}}</span>
                </div>
                <div class="IDcardNo">
                    {{user.cardType}}：<span>{{user.cardNo}}</span>
                </div>
            </div>
        </div>
        <div class="safePlan ">
            <div class="redeemNo row">
                <div class="col-sm col-sm-5 Tl">保障卡兑换码</div>
                <div class="col-sm col-sm-7 Tr"><input class="input" v-model.number="code" type="text" placeholder="如有保障卡请输入兑换码兑换"></div>
            </div>
            <div class="u">
                <div class="y affirm">
                  <div class="round-wrap" @click="isOk">
                    <i class="iconfont icon-tick" v-show="isOkStr"></i>
                  </div>
                  <em class="i-r">我已确认</em>
                  <span @click="goto(1)">投保须知</span>
                  <span class="wrap">
                    <a :href="form.insuranceProductClause" target="_blank">保险条款</a>
                  </span>
                </div>
            </div>
            <div class="integral-j u">
                <div class="y integral">需：<span>{{form.insuranceAmount}}积分</span></div>
            </div>
            <div class="integral-j u">
                <div class="y integral">剩余积分：<span>{{user.freeAmount}}积分</span></div>
            </div>
            <button class="submit-btn-1" @click="claim">领取</button>
        </div>
        <div class="dialog" v-if="isDialog">
          <div class="mask"></div>
          <div class="model">
              <div class="insure">确定投保？</div>
              <div class="icon1 row">
                  <div class="col-sm col-sm-6 Tl"><i class="iconfont icon-round-tick" @click="confirm(1)"></i></div>
                  <div class="col-sm col-sm-6 Tr"><i class="iconfont icon-close-blod" @click="confirm(2)"></i></div>
              </div>
          </div>
        </div>
        <div class="resultDia" v-if="isResult">
          <div class="mask"></div>
          <div class="model">
            <div class="success">投保成功</div>
            <div class="cont">
              <i class="iconfont icon-smiling-blod"></i>
            </div>
            <div class="foot">
              <i class="iconfont icon-close-blod" @click="goto(3)"></i>
            </div>
          </div>
        </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            un: null,
            form: {},
            pageType: null,
            list: [],
            note: require('@/assets/images/productDetail.png'),
            id: null,
            isDialog: false,
            isResult: false,
            integral: 10,
            code: null,
            params: {
                personFirstAccount: null
            },
            user: {},
            isOkStr: true
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.params.personFirstAccount = pfa);
            if (localStorage.getItem('appUserInfo')) {
                this.un = JSON.parse(localStorage.getItem('appUserInfo')).cookieResp.un;
            }
            this.pageType = this.$route.query.type;
            this.getForm();
            this.getUser();
        },
        getForm () {
            // 加了从续保进入的判断
            if (this.pageType === 'xubao') {
                this.params['insuranceProductId'] = this.$route.query.insuranceProductId;
            } else {
                this.params['insuranceProductId'] = this.$route.query.id;
            }
            this.$http.post(this.$api.insurance.queryInsuranceProductDetailById, this.params).then(res => {
                if (res.data.code === 0) {
                    this.form = res.data.data;
                    this.list = res.data.data.list;
                }
            });
        },
        isOk () {
            this.isOkStr = !this.isOkStr;
        },
        getUser () {
            this.$http.post(this.$api.insurance.queryPersonInfo, {personFirstAccount: this.params.personFirstAccount}).then(res => {
                if (res.data.code === 0) {
                    this.user = res.data.data;
                }
            });
        },
        goto (type) {
            // 1 投保须知
            if (type === 1) {
                this.$router.push({name: 'noticeInsure'});
            }
            // 2 保障计划
            if (type === 2) {
                this.$router.push({name: 'planDetails', query: this.form});
            }
            // 3 投保成功
            if (type === 3) {
                this.$router.push({name: 'naturalHome'});
            }
        },
        // 领取
        claim () {
            if (!this.un) {
                this.$mint.Toast({
                    message: '请先实名认证',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (!this.isOkStr) {
                this.$mint.Toast({
                    message: '请认真阅读《投保须知》和《保险条款》',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (!this.code) {
                if (this.user.freeAmount < this.form.insuranceAmount) {
                    this.$mint.Toast({
                        message: '当前积分不足，不能领取',
                        position: 'top',
                        duration: 1000
                    });
                    return false;
                }
            }
            this.isDialog = true;
        },
        // 确定投保
        // type 1 确认 2 取消
        confirm (type) {
            if (type === 1) {
                let params = {
                    personFirstAccount: this.params.personFirstAccount,
                    insuranceProductId: this.$route.query.id
                };
                if (this.code) {
                    params['activationCode'] = this.code;
                }
                this.$http.post(this.$api.insurance.applyInsuranceByPerson, params).then(res => {
                    if (res.data.code === 0) {
                        this.isResult = true;
                    }
                });
            }
            this.isDialog = false;
        }
    }
};
</script>
<style lang="scss" scoped>
  .listL {
    a {
      text-decoration: none;
      color: #000000;
    }
  }
  .wrap {
    display: inline-block;
  }
  .headerImg {
    width: 100%;
    height: px2rem(84);
    min-height: px2rem(84);
  }
  .authentic {
    .Tl span{
      vertical-align: middle;
      padding-left: 5px;
    }
  }
  .redeemNo {
    line-height: px2rem(50);
    .Tr {
      text-align: right;
    }
  }
  .affirm {
    .i-r, span {
      vertical-align: middle;
    }
  }
  .affirm {
    span a {
      color: #72b81e;
      text-decoration: none;
    }
  }
  .resultDia {
    text-align: center;
    position: relative;
    height: px2rem(100);
    .success {
      width: 100%;
      height: px2rem(20);
    }
    .cont {
      margin: px2rem(10) 0;
      .icon-smiling-blod:before {
        font-size: 60px;
        color: #72b81e;
      }
    }
    .foot {
      position: absolute;
      bottom: px2rem(-35);
      left: 50%;
      color: #fff;
      transform: translateX(-50%);
      .icon-close-blod:before {
        font-size: 20px;
      }
    }
  }
  .round-wrap {
    width: 18px;
    height: 18px;
    display: inline-block;
    border-radius: 50%;
    border: 1px solid #72b81e;
    position: relative;
    vertical-align: middle;
    .icon-tick {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    .icon-tick:before {
      font-size: 14px;
    }
  }
  .insuranceIntro {
    padding-bottom: 2rem;
  }
</style>



