<template>
    <section class="insure-origin insure-origi-natural">
      <div v-if="isForm">
        <div class="lc-tip row">
          <div class="col-sm col-sm-8 l">
            <div class="block"></div>
            <span>本保险保障{{form.fromName}}</span>
          </div>
        </div>
        <div class="page-top">
            <div class="form">
                <div class="item row">
                    <div class="col-sm col-sm-4 l">保险保障名称</div>
                    <div class="col-sm col-sm-8 r">{{form.insuranceProductName}}</div>
                </div>
                <div class="item row">
                    <div class="col-sm col-sm-2 l">保单号</div>
                    <div class="col-sm col-sm-10 r">
                      <!-- 9.20 修改为由后台返回 -->
                      <!-- <span v-if="form.status !== 10">{{form.insurancePersonalNo}}</span>
                      <span v-else>投保中，请第二天查询</span> -->
                      <span>{{form.insurancePersonalNo}}</span>
                    </div>
                </div>
                <div class="item row">
                    <div class="col-sm col-sm-2 l">有效期</div>
                    <div class="col-sm col-sm-10 r">
                        <!-- 9.20 修改为由后台返回 -->
                        <!-- <span v-if="form.status !== 10">
                          <span v-if="form.periodStart && form.periodEnd">{{form.periodStart | date}}至{{form.periodEnd | date}}</span>
                        </span>
                        <span v-else>投保中，请第二天查询</span> -->
                        <span>{{form.validityPeriodStr}}</span>
                    </div>
                </div>
                <div class="item row" v-if="form.status !== 10">
                    <div class="col-sm col-sm-4 l">理赔客服电话</div>
                    <div class="col-sm col-sm-8 r"><a :href="'tel:' + form.claimMobile">{{form.claimMobile}}</a></div>
                </div>
                <div class="item row" v-if="form.status !== 10">
                    <div class="col-sm col-sm-1 l"></div>
                    <div class="col-sm col-sm-11 r">
                      <button class="btn-1" @click="goPage">我要理赔</button>
                      <button class="btn-2" @click="come">理赔记录</button>
                    </div>
                </div>
            </div>
        </div>
      </div>
      <div v-else class="receive">
          <button @click="receive">我要领取</button>
      </div>
      <div class="page-bottom">
        <div class="lc-tip row">
          <div class="col-sm col-sm-8 l">
            <div class="block"></div>
            <span>保险保障推荐</span>
          </div>
          <div class="col-sm col-sm-4 r" @click="jump">更多<i class="iconfont icon-arrow-right"></i></div>
        </div>
        <section class="list" v-if="isList">
          <ul>
            <li v-for="(item, index) in list" :key="index" @click="goto(item.id)">
              <span class="row">
                <div class="col-sm col-sm-2 l">
                    <img :src="item.logo" alt="">
                  </div>
                  <div class="col-sm col-sm-9 c">
                    <div class="title">{{item.name}}</div>
                    <div>{{item.demo}}</div>
                  </div>
                  <div class="col-sm col-sm-1 r">
                    <i class="iconfont icon-arrow-right"></i>
                  </div>
              </span>
            </li>
          </ul>
        </section>
        <div class="home-error" v-if="!list">
          <img src="../../assets/images/license-error.png" alt="">
          <p>暂无保险保障推荐</p>
        </div>
      </div>
      <div class="button-wrap">
        <button class="submit-btn-1" @click="my">我 的</button>
      </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            personFirstAccount: null,
            list: [],
            isList: null,
            form: {},
            isForm: null
        };
    },
    created () {
        this.init();
    },
    mounted () {
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.personFirstAccount = pfa);
            this.$http.post(this.$api.insurance.queryPersonInsuranceIndex, {personFirstAccount: this.personFirstAccount}).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.list;
                    this.form = res.data.data.insurancePersonal;
                    if (this.list && this.list.length > 0) {
                        this.isList = true;
                    } else {
                        this.isList = false;
                    }
                    if (this.form) {
                        this.isForm = true;
                    } else {
                        this.isForm = false;
                    }
                }
            });
        },
        // 更多
        jump () {
            this.$router.push({name: 'naturalMore', query: {personFirstAccount: this.personFirstAccount}});
        },
        // 我要领取
        receive () {
            this.$router.push({name: 'details', query: {id: this.list[0].id}});
        },
        // 详情
        goto (id) {
            this.$router.push({name: 'details', query: {id: id}});
        },
        // 入口
        come () {
            this.$router.push({name: 'query'});
        },
        // 我要理赔
        goPage () {
            this.$router.push({name: 'beforeApplying', query: this.form});
        },
        my () {
            this.$router.push({name: 'my'});
        }
    }
};
</script>
<style lang="scss" scoped>
  .page-top {
    .form {
      a {
        text-decoration: none;
      }
    }
  }
  .home-error {
    margin-top: px2rem(20);
    text-align: center;
    img {
      width: px2rem(180);
      height: px2rem(155);
    }
    p {
      color: #999999;
      margin-top: px2rem(20);
    }
  }
  .receive {
    padding: 1rem 0.5rem 0;
    text-align: right;
    button {
      color: #fff;
      border: 0;
      padding: px2rem(2) px2rem(6);
      background-color: #72b81e;
    }
  }
</style>



