<template>
  <section class="insure-my">
    <!-- <div class="line"></div> -->
    <ul>
        <li @click="jump(1)">
          <span class="link row">
              <div class="col-sm col-sm-6 l">保单</div>
              <div class="col-sm col-sm-6 r">
                <i class="iconfont icon-arrow-right"></i>
              </div>
          </span>
        </li>
        <li @click="jump(2)">
          <span class="link row">
              <div class="col-sm col-sm-6 l">理赔</div>
              <div class="col-sm col-sm-6 r">
                <i class="iconfont icon-arrow-right"></i>
              </div>
          </span>
        </li>
    </ul>
  </section>
</template>

<script>
export default {
    data () {
        return {
        };
    },
    created () {
    },
    methods: {
        jump (type) {
            type === 1 ? this.$router.push({name: 'policy'}) : this.$router.push({name: 'claim'});
        }
    }
};
</script>



