<template>
    <section class="insure-upload">
      <ul>
          <li>
            <div class="t row">
              <div class="col-sm col-sm-6 l">身份证正反面</div>
              <!-- <div class="col-sm col-sm-6 r">查看样张</div> -->
            </div>
            <div class="block" v-if="form.cardNoImgs.length > 0">
              <div class="img-box" v-for="(item, index) in form.cardNoImgs" :key="index">
                <img :src="item" alt="" @click="showImg(item)">
                <i class="iconfont icon-close-blod" @click="dele(index, form.cardNoImgs)"></i>
              </div>
            </div>
            <div class="c" v-if="form.cardNoImgs.length < 2">
              <label for="file1" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file1" runat="server" ref="inputer1" accept="image/*" class="inputfile" @change="uploadImg(1, form.cardNoImgs)" />
              <!-- <input type="file" accept="image/*" mutiple="mutiple" capture="camera" /> -->
            </div>
            <div class="b">已上传 <span>{{form.cardNoImgs.length}}</span> 张照片，必须上传 <span>2</span> 张照片</div>
          </li>
          <li>
            <div class="t row">
              <div class="col-sm col-sm-6 l">医疗费用发票 </div>
              <!-- <div class="col-sm col-sm-6 r">查看样张</div> -->
            </div>
            <div class="block" v-if="form.costImgs.length > 0">
              <div class="img-box" v-for="(item, index) in form.costImgs" :key="index">
                <img :src="item" alt="" @click="showImg(item)">
                <i class="iconfont icon-close-blod" @click="dele(index, form.costImgs)"></i>
              </div>
            </div>
            <div class="c" v-if="form.costImgs.length < 10">
              <label for="file2" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file2" ref="inputer2" accept="image/png, image/jpeg, image/gif, image/jpg" class="inputfile" @change="uploadImg(2, form.costImgs)" />
            </div>
            <div class="b">已上传 <span>{{form.costImgs.length}}</span> 张照片，最多上传 <span>10</span> 张照片</div>
          </li>
          <li>
            <div class="t row">
              <div class="col-sm col-sm-6 l">事故证明 </div>
              <!-- <div class="col-sm col-sm-6 r">查看样张</div> -->
            </div>
            <div class="block" v-if="form.accidentImgs.length > 0">
              <div class="img-box" v-for="(item, index) in form.accidentImgs" :key="index">
                <img :src="item" alt="" @click="showImg(item)">
                <i class="iconfont icon-close-blod" @click="dele(index, form.accidentImgs)"></i>
              </div>
            </div>
            <div class="c" v-if="form.accidentImgs.length < 10">
              <label for="file3" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file3" ref="inputer3" accept="image/png, image/jpeg, image/gif, image/jpg" class="inputfile" @change="uploadImg(3, form.accidentImgs)" />
            </div>
            <div class="b">已上传 <span>{{form.accidentImgs.length}}</span> 张照片，最多上传 <span>10</span> 张照片</div>
          </li>
          <li>
            <div class="t row">
              <div class="col-sm col-sm-9 l">门诊病例或者门诊检查报告<span class="small">（如有请上传）</span> </div>
              <!-- <div class="col-sm col-sm-3 r">查看样张</div> -->
            </div>
            <div class="block" v-if="form.caseReportImgs.length > 0">
              <div class="img-box" v-for="(item, index) in form.caseReportImgs" :key="index">
                <img :src="item" alt="" @click="showImg(item)">
                <i class="iconfont icon-close-blod" @click="dele(index, form.caseReportImgs)"></i>
              </div>
            </div>
            <div class="c" v-if="form.caseReportImgs.length < 10">
              <label for="file4" class='div'><i class="iconfont icon-updata-add"></i></label>
              <input type="file" id="file4" ref="inputer4" accept="image/png, image/jpeg, image/gif, image/jpg" class="inputfile" @change="uploadImg(4, form.caseReportImgs)" />
            </div>
            <div class="b">已上传 <span>{{form.caseReportImgs.length}}</span> 张照片，最多上传 <span>10</span> 张照片</div>
          </li>
      </ul>
      <div class="yes">
          <i class="iconfont icon-round-tick"></i>
          <span>我已确认</span>
      </div>
      <div class="button-wrap-new">
        <button class="submit-btn-1" @click="submit">提交</button>
      </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            fil: null,
            form: {
                cardNoImgs: [], // 身份证
                costImgs: [], // 医疗费用发票
                accidentImgs: [], // 事故证明
                caseReportImgs: [] // 门诊病例或门诊检查报告
            },
            query: {}
        };
    },
    created () {
        this.query = this.$route.query;
    },
    methods: {
        showImg (url) {
            this.$mint.MessageBox({
                title: '预览图片',
                message: `<img src="${url}">`
            });
        },
        uploadImg (type, list) {
            let inputDOM;
            if (type === 1) {
                inputDOM = this.$refs.inputer1;
            }
            if (type === 2) {
                inputDOM = this.$refs.inputer2;
            }
            if (type === 3) {
                inputDOM = this.$refs.inputer3;
            }
            if (type === 4) {
                inputDOM = this.$refs.inputer4;
            }
            // 通过DOM取文件数据
            this.fil = inputDOM.files;
            let formData = new FormData();
            let headers = {
                'Content-Type': 'multipart/form-data'
            };
            formData.append('file', this.fil[0]);
            formData.append('storage', 'platform-mgmt');
            formData.append('fileName', this.fil[0].name);
            this.$http.post('https://news.shcvs.cn/cm/upload/uploadImgFolderOSS?folderName=insurance', formData, headers).then(res => {
                if (res.data.errorcode === 0) {
                    list.push(res.data.data);
                }
            }).catch((e) => {
                alert(e);
            });
        },
        dele (idx, list) {
            list.splice(idx, 1);
        },
        submit () {
            if (!this.check()) return false;
            let params = {
                cardNoImgs: '', // 身份证
                costImgs: '', // 医疗费用发票
                accidentImgs: '', // 事故证明
                caseReportImgs: '' // 门诊病例或门诊检查报告
            };
            for (let i in this.form) {
                params[i] = this.form[i].join(';');
            }
            Object.assign(this.query, params);
            console.log(this.query);
            this.$http.post(this.$api.insurance.insurancePersonClaim, this.query).then(res => {
                if (res.data.code === 0) {
                    this.$router.push({name: 'naturalHome'});
                }
            });
        },
        check () {
            if (this.form.cardNoImgs.length < 2) {
                this.$mint.Toast({
                    message: '请上传身份证正反面',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.costImgs.length === 0) {
                this.$mint.Toast({
                    message: '请上传医疗费用发票',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.accidentImgs.length === 0) {
                this.$mint.Toast({
                    message: '请上传事故证明',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            return true;
        }
    }
};
</script>
<style lang="scss" scoped>
  .insure-upload {
    .inputfile {
      position:absolute;clip:rect(0 0 0 0);
    }
    li {
      .block {
        margin-top: px2rem(15);
        .img-box {
          position: relative;
          display: inline-block;
          img {
            width: px2rem(60);
            height: px2rem(60);
            display: inline-block;
            margin-right: 5px;
            margin-bottom: 15px;
          }
          i {
            position: absolute;
            top: -2px;
            right: 7px;
          }
          .icon-close-blod:before {
            color: red;
          }
        }
      }
    }
  }
</style>



