<template>
  <section class="insure-selectUser">
    <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
      <header v-if="list.length > 0">
        <div class="search">
            <input type="text" placeholder="姓名或身份证号" v-model="form.content">
            <i class="iconfont icon-loupe" @click="init"></i>
            <span class="cencle" @click="cencle">取消</span>
        </div>
      </header>
      <div class="list">
        <ul>
            <li v-for="(item, index) in list" :key="index" class="bg row select-bg" :style="{backgroundImage: 'url('+note+')'}" @click="elect(item)">
              <div class="col-sm col-sm-10">
                <span class="round-1" :class="{'active': item.check}">
                  <i v-if="item.check" class="iconfont icon-tick"></i>
                </span>
                <div class="info">
                    <div>姓&nbsp;&nbsp;名：<span>{{item.realName}}</span></div>
                    <div>{{item.cardType}}：<span>{{item.cardNo}}</span></div>
                </div>
              </div>
              <div v-if="item.insuranceStatus === -30" class="col-sm col-sm-2">未购买</div>
              <div v-if="item.insuranceStatus === 30" class="col-sm col-sm-2">可续保</div>
            </li>
        </ul>
      </div>
    </mt-loadmore>
    <div class="button-wrap" v-if="list.length > 0">
      <div class="submit-btn-1" @click="submit">捐赠保险保障</div>
    </div>
    <div class="error" v-else>
      <img src="../../assets/images/license-error.png" alt="">
      <p>暂无用户数据</p>
    </div>
    <div v-if="isDialog">
      <div class="mask"></div>
      <div class="model">
        <div class="t">
          确认信息
        </div>
        <div class="item row">
            <div class="col-sm col-sm-3 l">保障名称</div>
            <div class="col-sm col-sm-9 r">{{name}}</div>
        </div>
        <div class="button-W row">
            <div class="col-sm col-sm-6 l">
              <button @click="confirm()">确 认</button>
            </div>
            <div class="col-sm col-sm-6 r">
              <button @click="isDialog = false">取 消</button>
            </div>
        </div>
      </div>
    </div>
    <!-- 投保失败 -->
    <div class="model model-error" v-if="isError">
      <div class="t">投保失败</div>
      <div class="c">
        <i class="iconfont icon-cry"></i>
      </div>
      <div class="b">
        <span>失败原因：{{isErrorForm.msg}}</span>
      </div>
      <div class="close">
        <i class="iconfont icon-close" @click="isError = false"></i>
      </div>
    </div>
  </section>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/select.png'),
            isDialog: false,
            isError: false,
            list: [],
            isErrorForm: {
                msg: ''
            },
            form: {
                orgThirdAccount: '',
                pageSize: 10,
                pageNumber: 1,
                insuranceProductId: '', // 产品id
                content: null
            },
            info: {},
            name: null,
            allLoaded: false,
            bottomText: '上拉加载更多...',
            orgThirdAccount: ''
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            // 线上
            this.form.orgThirdAccount = localStorage.getItem('orgThirdAccount');
            // 本地
            // this.form.orgThirdAccount = 'BBA101421';
            this.name = this.$route.query.name;
            this.form.insuranceProductId = this.$route.query.insuranceProductId;
            // 用户list
            this.$http.post(this.$api.insurance.queryMembersForVerify, this.form).then(res => {
                if (res.data.code === 0) {
                    res.data.data.records.forEach(e => {
                        e['check'] = false;
                    });
                    this.list = res.data.data.records;
                }
            });
        },
        cencle () {
            this.form.content = null;
            this.init();
        },
        elect (item) {
            item.check = !item.check;
        },
        submit () {
            let arr = [];
            this.list.forEach(e => {
                if (e.check) {
                    arr.push(e);
                }
            });
            if (arr.length > 0) {
                this.isDialog = true;
                this.info = {
                    insuranceOrgId: '',
                    list: arr
                };
                return;
            }
            this.$mint.Toast({
                message: '请选择用户',
                position: 'top',
                duration: 1000
            });
        },
        confirm () {
            let arr = [];
            this.list.forEach(e => {
                if (e.check) {
                    arr.push({personFirstAccount: e.personFirstAccount});
                }
            });
            // insuranceOrgId 改为 insuranceProductId
            let params = {
                insuranceProductId: this.form.insuranceProductId,
                orgThirdAccount: this.form.orgThirdAccount,
                list: arr
            };
            this.$http.post(this.$api.insurance.buyInsuranceOrgToPersonal, params).then(res => {
                this.isDialog = false;
                if (res.data.code === 0) {
                    this.$mint.Toast({
                        message: res.data.msg,
                        position: 'top',
                        duration: 1000
                    });
                    this.$router.push({name: 'index'});
                    return;
                }
                this.isError = true;
                this.isErrorForm.msg = res.data.msg;
            });
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.insurance.queryMembersForVerify, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        }
    }
};
</script>
<style lang="scss" scoped>
  .bg {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
  }
  .insure-selectUser {
    padding-bottom: 100px;
  }
  .insure-selectUser .model-error .close {
    border: 1px solid #000000;
    .icon-close {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
</style>



