<template>
  <div class="body">
    <div class="box">
        <section class="noticeInsure claimExplain">
            <section class="entirety">
                <i class="i iconfont icon-jay"></i>
                <div class="claimText">
                    我们将会给您发送索赔指引邮件,
                    告知您资料要求、理赔流程及纸质材料的寄送地址。
                    如您不确定现有材料是否符合理赔要求,您可以将所有材料
                    拍照发送至，我们将会协助您进行影像件预审核
                </div>
            </section>
            <div class="introBtn">
                <button class="submit-btn-1" @click="jump">我知道了</button>
            </div>
        </section>
    </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
        };
    },
    created () {
        if (this.$route.query.type === '1') {
            this.$route.meta.title = '购买须知';
        }
    },
    methods: {
        jump () {
            this.$router.go(-1);
        }
    }
};
</script>



