<template>
    <section class="insure-insured">
        <header :style="{backgroundImage: 'url('+note+')'}">
          <div class="search">
            <input type="text" placeholder="姓名、身份证号、保单号" v-model="form.searchContent">
            <i class="iconfont icon-loupe" @click="init"></i>
          </div>
          <!-- 产品 -->
          <div class="product">
            <div class="prod">
              <div class="t">{{info.insuranceProductName}}</div>
              <div class="b">
                <div class="explain">{{info.insuranceProductDemo}}</div>
                <!-- <div class="item row it-1">
                    <div class="col-sm col-sm-6 m-l">总保单：{{info.total}}</div>
                    <div class="col-sm col-sm-6 m-r">剩余保单：{{info.remaining}}</div>
                </div> -->
                <div class="item row">
                    <div class="col-sm col-sm-6 m-l">已投保：{{info.used}}</div>
                    <div class="col-sm col-sm-6 m-r">投保失败：{{info.failure}}</div>
                </div>
              </div>
            </div>
          </div>
          <!-- 列表 -->
          <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
            <ul>
              <li v-for="(item, index) in list" :key="index">
                <div class="t row">
                  <div class="col-sm col-sm-6 l">姓名 : {{item.realName}}</div>
                  <div class="col-sm col-sm-6 r active-1" v-if="item.status === 10">{{item.status | status}}</div>
                  <div class="col-sm col-sm-6 r" v-if="item.status === 20" :class="{'active-2': item.status === 20}">{{item.status | status}}</div>
                  <div class="col-sm col-sm-6 r" v-if="item.status === -10" :class="{'active-3': item.status === -10}">{{item.status | status}}</div>
                </div>
                <!-- 审核中 -->
                <div class="b" v-if="item.status === 10">
                    <div class="idCard">{{item.cardType}}：{{item.cardNo}}</div>
                    <div>保单号：{{item.insurancePersonalNo}}</div>
                </div>
                <!-- 保障中 -->
                <div class="b-b" v-if="item.status === 20">
                    有效期：{{item.periodStart | date}} 至 {{item.periodEnd | date}}
                </div>
                <!-- 失败 -->
                <div class="b" v-if="item.status === -10">
                    <div class="idCard"><div class="l">{{item.cardType}}</div>：{{item.cardNo}}</div>
                </div>
                <div class="b-b my-b-b" v-if="item.status === -10">
                  <span class="error-text">失败说明：</span>{{item.reason}}
                </div>
              </li>
            </ul>
          </mt-loadmore>
        </header>
    </section>
</template>

<script>
export default {
    data () {
        return {
            isShow: false,
            note: require('@/assets/images/publicity-header.png'),
            form: {
                pageNumber: 1,
                pageSize: 10,
                searchContent: null,
                insuranceProductId: null,
                orgThirdAccount: null
            },
            info: {},
            allLoaded: false,
            bottomText: '上拉加载更多...',
            list: []
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            this.info = this.$route.query;
            this.form.insuranceProductId = this.info.insuranceProductId;
            this.form.orgThirdAccount = this.info.orgThirdAccount;
            this.$http.post(this.$api.insurance.queryOrgInsurancePersonal, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.insurance.queryInsuranceProductByOrgRecommend, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        }
    }
};
</script>
<style lang="scss" scoped>
  .insure-insured {
      .search {
        input {
          padding-left: px2rem(10);
        }
      }
      .product .prod .t {
        padding-left: 0;
      }
      .idCard {
        .l {
          color: #666666;
          display: inline-block;
        }
      }
  }
</style>



