<template>
    <section class="insure-origin">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
        <section class="list">
          <ul>
              <li v-for="(item, index) in list" :key="index" @click="jump(item)">
                <span class="link row">
                  <div class="col-sm col-sm-2 l">
                      <img :src="item.logo" alt="">
                    </div>
                    <div class="col-sm col-sm-9 c">
                      <div class="title">{{item.name}}</div>
                      <div>{{item.demo}}</div>
                    </div>
                    <div class="col-sm col-sm-1 r">
                      <i class="iconfont icon-arrow-right"></i>
                    </div>
                </span>
              </li>
          </ul>
        </section>
      </mt-loadmore>
      <div class="button-wrap">
        <button class="submit-btn-1" @click="goto">我 的</button>
      </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            form: {
                pageNumber: 1,
                pageSize: 10
            },
            list: [],
            allLoaded: false,
            bottomText: '上拉加载更多...'
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            this.$http.post(this.$api.insurance.queryInsuranceProductByOrgRecommend, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.insurance.queryInsuranceProductByOrgRecommend, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        },
        jump (item) {
            this.$router.push({name: 'insuranceIntro', query: {id: item.id, name: item.name}});
        },
        goto () {
            this.$router.push({name: 'myInsurance'});
        }
    }
};
</script>
<style lang="scss" scoped>
  .prdListWrap {
    position: absolute;
    left:0;
    top: 0;
    width:100%;
    height:100%;
    overflow:auto;
    box-sizing:border-box;
    padding-bottom: px2rem(100);
    -webkit-overflow-scrolling: touch;
  }
</style>



