<template>
  <section class="mui-content noticeInsure policy-urgency">
    <header class="lc-tips">
        <i class="iconfont icon-jay"></i>无法联系你本人时，联系该紧急联系人
    </header>
    <section class="form">
        <div class="item row">
          <div class="col-sm col-sm-4 l">紧急联系人</div>
          <input class="col-sm col-sm-8 r" type="text" placeholder="填写紧急联系人姓名" v-model="form.urgentName">
        </div>
        <div class="item row">
          <div class="col-sm col-sm-4 l">手机号码</div>
          <input class="col-sm col-sm-8 r" type="tel" placeholder="填写紧急联系人手机号码" v-model="form.urgentMobile">
        </div>
        <div class="item row">
          <div class="col-sm col-sm-4 l">紧急联系人</div>
          <!-- <input class="col-sm col-sm-8 r" type="text" placeholder="选择与被保人关系" v-model="form.urgentBind"> -->
          <div class="col-sm col-sm-8 r" @click="change">选择与被保人关系</div>
        </div>
    </section>
    <div class="line"></div>
    <section class="select-wrap" v-if="isShow">
      <div class="title">
        <i class="iconfont icon-round-tick"></i>
        <span>发送投保通知短信至紧急联系人</span>
      </div>
      <div class="select">
        <div v-for="(item, index) in list" :key="index" class="item" :class="{'active': tabIdx === item.id}" @click="tab(item)">{{item.value}}</div>
      </div>
    </section>
    <button class="submit-btn-1" @click="submit">提交</button>
  </section>
</template>

<script>
export default {
    data () {
        return {
            tabIdx: 1,
            isShow: false,
            form: {
                id: null,
                urgentName: null,
                urgentMobile: null,
                urgentBind: null
            },
            list: [
                {
                    id: 1,
                    value: '配偶'
                },
                {
                    id: 2,
                    value: '父母'
                },
                {
                    id: 3,
                    value: '子女'
                },
                {
                    id: 4,
                    value: '朋友'
                },
                {
                    id: 5,
                    value: '亲戚'
                }
            ]
        };
    },
    created () {
        this.form.id = Number(this.$route.query.id);
    },
    methods: {
        tab (item) {
            this.tabIdx = item.id;
            this.form.urgentBind = item.value;
        },
        change () {
            this.isShow = !this.isShow;
        },
        submit () {
            if (!this.check()) return false;
            this.$http.post(this.$api.insurance.setUrgentPerson, this.form).then(res => {
                if (res.data.code === 0) {
                    this.$router.go(-1);
                }
            });
        },
        check () {
            if (this.form.urgentName === null) {
                this.$mint.Toast({
                    message: '请填写紧急联系人姓名',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (!(/^(0|86|17951)?(13[0-9]|15[0-9]|166|17[3678]|18[0-9]|19[0-9]|14[57])[0-9]{8}$/.test(this.form.urgentMobile))) {
                this.$mint.Toast({
                    message: '请填写正确的手机号码',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.urgentBind === null) {
                this.$mint.Toast({
                    message: '请选择与被保人关系',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            return true;
        }
    }
};
</script>
<style lang="scss" scoped>
  .select-wrap {
    .title {
      color: #000;
      padding: px2rem(15) px2rem(13);
      .icon-round-tick:before {
        color: #72b81e ;
      }
      span {
        display: inline-block;
        vertical-align: middle;
      }
    }
    .select {
      margin: 0 px2rem(30);
      font-size: 0;
      .item {
        width: 32%;
        color: #72b81e;
        text-align: center;
        display: inline-block;
        height: px2rem(35);
        line-height: px2rem(35);
        font-size: 14px;
        vertical-align: middle;
        border: 1px solid #72b81e;
      }
      .item:nth-child(1) {
        border-right: 0;
        border-bottom: 0;
      }
      .item:nth-child(2) {
        border-bottom: 0;
      }
      .item:nth-child(3) {
        border-left: 0px;
      }
      .item:nth-child(4) {
        border-right: 0;
        position: relative;
        top: -1px;
      }
      .item:nth-child(5) {
        position: relative;
        top: -1px;
      }
      .active {
        color: #fff;
        background-color: #72b81e;
      }
    }
  }
</style>



