import beforeApplying from '../views/naturalPerson/beforeApplying.vue'; // 我的理赔
import information from '../views/naturalPerson/information.vue'; // 填写理赔信息
import oder from '../views/naturalPerson/oder.vue'; // 更多
import memo from '../views/naturalPerson/memo.vue'; // 随手牵
