<template>
  <section class="claims-on-line-2">
      <div class="form form-1">
            <div class="item">
                <div class="wrap row">
                  <!-- TODO 姓名手机号都用登录的 -->
                  <div class="col-sm col-sm-4 l">申请人姓名</div>
                  <!-- <input v-model="form.applyUserName" type="text" placeholder="请输入" class="col-sm col-sm-8 r"> -->
                  <div class="col-sm col-sm-8 r">{{userForm.realName}}</div>
                </div>
            </div>
            <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-6 l">申请人手机号码</div>
                  <!-- <input v-model="form.applyUserMobile" type="tel" maxlength="11" placeholder="请输入" class="col-sm col-sm-8 r"> -->
                  <div class="col-sm col-sm-6 r">{{userForm.mobile}}</div>
                </div>
            </div>
            <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-4 l">申请人证件号</div>
                  <div class="col-sm col-sm-8 r">{{userForm.cardNo}}</div>
                </div>
            </div>
            <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-4 l">保单号</div>
                  <div class="col-sm col-sm-8 r">{{form.insuranceNo}}</div>
                </div>
            </div>
      </div>
      <!-- 申请人是否是出现 -->
      <div class="form form-2">
            <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-8 l">申请人是否是出险人</div>
                  <div class="col-sm col-sm-4 r"><mt-switch v-model="switchValue" class="mint-switch"></mt-switch></div>
                </div>
            </div>
            <div class="isSwitchTrue" v-if="switchValue">
              <div class="item my-isSwitchTrue">
                  <div class="wrap row">
                    <div class="col-sm col-sm-4 l">出险人姓名</div>
                    <div class="col-sm col-sm-8 r">{{userForm.realName}}</div>
                  </div>
              </div>
              <!-- TODO -->
              <div class="item">
                  <div class="wrap row">
                    <div class="col-sm col-sm-4 l">出险人手机号</div>
                    <div class="col-sm col-sm-8 r">{{userForm.mobile}}</div>
                  </div>
              </div>
              <div class="item">
                  <div class="wrap row">
                    <div class="col-sm col-sm-4 l">出险人证件号</div>
                    <div class="col-sm col-sm-8 r">{{userForm.cardNo}}</div>
                  </div>
              </div>
            </div>
            <!-- 是false -->
            <div class="isSwitchTrue" v-else>
              <div class="item my-isSwitchTrue">
                  <div class="wrap row">
                    <div class="col-sm col-sm-4 l">出险人姓名</div>
                    <input v-model="form.realName" type="text" placeholder="请输入" class="col-sm col-sm-8 r">
                  </div>
              </div>
              <div class="item">
                  <div class="wrap row">
                    <div class="col-sm col-sm-4 l">出险人手机号</div>
                    <input v-model="form.applyUserMobile" type="text" placeholder="请输入" class="col-sm col-sm-8 r">
                  </div>
              </div>
              <div class="item">
                  <div class="wrap row">
                    <div class="col-sm col-sm-4 l">出险人证件号</div>
                    <input v-model="form.cardNo" type="text" placeholder="请输入" class="col-sm col-sm-8 r">
                  </div>
              </div>
            </div>
      </div>
      <div class="form form-2">
        <mt-navbar v-model="selected">
          <mt-tab-item id="1" style="border-top-left-radius: 5px; border-bottom-left-radius: 5px;">活动信息</mt-tab-item>
          <mt-tab-item id="2" style="border-top-right-radius: 5px;border-bottom-right-radius: 5px;">随手签信息</mt-tab-item>
        </mt-navbar>
        <mt-tab-container v-model="selected">
          <!-- 活动信息 -->
          <mt-tab-container-item id="1" class="activity">
            <div class="box item" @click="goto(1)">
                  <div class="wrap row">
                    <div class="col-sm col-sm-4 ver">活动名称</div>
                    <div class="col-sm col-sm-7 ver last" v-if="queryForm.type === 'click'">{{activityForm.name}}</div>
                    <div class="col-sm col-sm-7 ver last" v-else>{{vTabShowForm.activityName}}</div>
                    <div class="col-sm col-sm-1 "></div>
                  </div>
                  <div class="wrap row">
                    <div class="col-sm col-sm-4 ver">活动地址</div>
                    <div class="col-sm col-sm-7 ver last" v-if="queryForm.type === 'click'">{{activityForm.address}}</div>
                    <div class="col-sm col-sm-7 ver last" v-else>{{vTabShowForm.activityAddress}}</div>
                    <div class="col-sm col-sm-1 "></div>
                  </div>
              <div class="right">
                <i class="iconfont icon-arrow-right"></i>
              </div>
            </div>
          </mt-tab-container-item>
          <!-- 随手签信息 -->
          <mt-tab-container-item id="2" class="activity">
            <div class="box item" @click="goto(2)">
                  <div class="wrap row">
                    <div class="col-sm col-sm-4 ver">随手签说明</div>
                    <div class="col-sm col-sm-7 ver last" v-if="queryForm.type === 'click'">{{suishouForm.content}}</div>
                    <div class="col-sm col-sm-7 ver last" v-else>{{vTabShowForm.activityName}}</div>
                    <div class="col-sm col-sm-1 "></div>
                  </div>
                  <div class="wrap row">
                    <div class="col-sm col-sm-4 ver">随手签地址</div>
                    <div class="col-sm col-sm-7 ver last" v-if="queryForm.type === 'click'">{{suishouForm.address}}</div>
                    <div class="col-sm col-sm-7 ver last" v-else>{{vTabShowForm.activityAddress}}</div>
                    <div class="col-sm col-sm-1 "></div>
                  </div>
              <div class="right">
                <i class="iconfont icon-arrow-right"></i>
              </div>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>
      <div class="form form-2">
            <!-- <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-4 l">出险人日期</div>
                  <div class="col-sm col-sm-8 r" @click="open">
                    <span v-if="isShow">选择</span>
                    <span v-else>{{form.outInsuranceTime | date}}</span>
                  </div>
                </div>
            </div> -->
            <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-4 l">出险日期</div>
                  <div class="col-sm col-sm-8 r">{{outInsuranceTime}}</div>
                </div>
            </div>
            <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-4 l">出险地址</div>
                  <input v-model="form.outInsuranceAddress" type="text" placeholder="请输入" class="col-sm col-sm-8 r">
                </div>
            </div>
            <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-4 l">理赔金额</div>
                  <div class="col-sm col-sm-8 r">
                    <input class="r" v-model="form.applyAmount" type="number" placeholder="请输入">
                    <span>万</span>
                  </div>
                </div>
            </div>
      </div>
      <div class="form form-3">
            <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-4 l">活动名称</div>
                  <input v-model="form.activityName" type="text" placeholder="请输入" class="col-sm col-sm-8 r">
                </div>
            </div>
      </div>
      <div class="form form-4">
            <div class="item">
                <div class="wrap row title">
                  <div class="col-sm col-sm-12 l">理赔账户(未成年人出险必须使用申请人的银行账户)</div>
                </div>
            </div>
            <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-4 l">持卡人姓名</div>
                  <input v-model="form.bankPerson" type="text" placeholder="请输入" class="col-sm col-sm-8 r">
                </div>
            </div>
            <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-4 l">银行卡号</div>
                  <input v-model="form.bankNo" type="tel" placeholder="请输入" class="col-sm col-sm-8 r">
                </div>
            </div>
            <div class="item">
                <div class="wrap row">
                  <div class="col-sm col-sm-4 l">开户银行</div>
                  <input v-model="form.bankName" type="text" placeholder="请输入" class="col-sm col-sm-8 r">
                </div>
            </div>
      </div>
      <div class="form form-5">
            <div class="item">
                <div class="wrap row title">
                    <div class="col-sm col-sm-12 l">理赔说明</div>
                </div>
            </div>
            <div class="item">
                <div class="wrap row wrap-2">
                    <textarea placeholder="多行输入" cols="30" rows="10" v-model="form.accidentDemo"></textarea>
                </div>
            </div>
      </div>
      <mt-datetime-picker
        ref="picker"
        type="date"
        v-model="value"
        @confirm="confirm"
        :endDate="endDate"
        placeholder="选择">
      </mt-datetime-picker>
      <div class="btn-wrap-new">
        <button class="submit-btn-1" @click="jump">下一步</button>
      </div>
  </section>
</template>

<script>
export default {
    data () {
        return {
            switchValue: true,
            selected: '1',
            userForm: {},
            form: {
                outInsuranceAddress: null,
                applyUserName: null,
                applyUserMobile: null,
                applyAmount: null,
                activityName: null,
                activityAddress: null,
                bankPerson: null,
                bankNo: null,
                bankName: null,
                accidentDemo: null,
                realName: null,
                outInsuranceTime: null,
                cardNo: null,
                insuranceNo: null
            },
            outInsuranceTime: null,
            personFirstAccount: null,
            isShow: true,
            value: null,
            endDate: null,
            oderForm: {
                months: 6, // 近几个月
                user: null,
                pageSize: 4,
                pageNumber: 1
            },
            vTabShowForm: {
                activityName: null,
                activityAddress: null
            },
            memoForm: {
                months: 6,
                userCode: null
            },
            queryForm: {},
            // 从内页返回携带的活动内容
            activityForm: {
                name: null,
                address: null
            },
            // 从内页返回携带的随手签内容
            suishouForm: {
                content: null,
                address: null
            }
        };
    },
    created () {
        let curDate = new Date();
        this.value = new Date(curDate.getTime() - 24 * 60 * 60 * 1000); // 前一天
        this.endDate = new Date(curDate.getTime() - 24 * 60 * 60 * 1000);
        let pfa = localStorage.getItem('personFirstAccount');
        pfa && (this.personFirstAccount = pfa);
        this.oderForm.user = this.personFirstAccount;
        this.memoForm.userCode = this.personFirstAccount;
        this.form.insuranceNo = this.$route.query.insurancePersonalNo;
        // this.$route.query.type === 'query' ? this.getForm() : this.inputForm();
        this.getForm();
        this.getUser();
        this.queryForm = this.$route.query;
    },
    methods: {
        // 获取用户信息
        getUser () {
            this.$http.post(this.$api.insurance.queryPersonInfo, {'personFirstAccount': this.personFirstAccount}).then(res => {
                if (res.data.code === 0) {
                    this.userForm = res.data.data;
                }
            });
        },
        // 获取理赔详情
        getForm () {
            this.$http.post(this.$api.insurance.queryClaimByInsuranceNo, {insuranceNo: this.form.insuranceNo}).then(res => {
                if (res.data.code === 0) {
                    let data = res.data.data;
                    this.vTabShowForm.activityName = data.activityName;
                    this.vTabShowForm.activityAddress = data.activityAddress;
                    // this.outInsuranceUser = data.outInsuranceUser;
                    // this.outInsuranceMobile = data.outInsuranceMobile;
                    // this.outInsuranceCardNo = data.outInsuranceCardNo;
                    if (this.$route.query.type === 'click') {
                        let storage1 = localStorage.getItem('oderForm');
                        if (storage1) {
                            storage1 = JSON.parse(storage1);
                            this.activityForm.name = storage1.name;
                            this.activityForm.address = storage1.address;
                        }
                        let storage2 = localStorage.getItem('memoForm');
                        if (storage2) {
                            storage2 = JSON.parse(storage2);
                            this.suishouForm.content = storage2.content;
                            this.suishouForm.address = storage2.address;
                        }
                    }
                }
            });
        },
        open () {
            this.$refs.picker.open();
        },
        confirm (v) {
            this.isShow = false;
            this.form.outInsuranceTime = this.date(v);
        },
        inputForm () {
            // this.form.realName = this.$route.query.realName;
            // this.form.cardNo = this.$route.query.cardNo;
        },
        jump () {
            if (!this.check()) return false;
            this.$router.push({name: 'upload', query: this.form});
        },
        goto (type) {
            // 活动订单列表
            if (type === 1) {
                this.$router.push({name: 'oder'});
            }
            // 随手牵列表
            if (type === 2) {
                this.$router.push({name: 'memo'});
            }
        },
        check () {
            if (!this.switchValue) {
                if (!this.form.realName) {
                    this.$mint.Toast({
                        message: '请输入出险人姓名',
                        position: 'top',
                        duration: 1000
                    });
                    return false;
                }
                if (!(/^(0|86|17951)?(13[0-9]|15[0-9]|166|17[3678]|18[0-9]|19[0-9]|14[57])[0-9]{8}$/.test(this.form.applyUserMobile))) {
                    this.$mint.Toast({
                        message: '请输入正确的出险人手机号',
                        position: 'top',
                        duration: 1000
                    });
                    return false;
                }
                if (!this.form.cardNo) {
                    this.$mint.Toast({
                        message: '请输入出险人证件号',
                        position: 'top',
                        duration: 1000
                    });
                    return false;
                }
            }
            if (!this.form.outInsuranceAddress) {
                this.$mint.Toast({
                    message: '请输入出险地址',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.applyAmount === null) {
                this.$mint.Toast({
                    message: '请输入理赔金额',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.activityName === null) {
                this.$mint.Toast({
                    message: '请输入活动名称',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.bankPerson === null) {
                this.$mint.Toast({
                    message: '请输入持卡人姓名',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.bankNo === null) {
                this.$mint.Toast({
                    message: '请输入银行卡号',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.bankName === null) {
                this.$mint.Toast({
                    message: '请输入开户银行',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.accidentDemo === null) {
                this.$mint.Toast({
                    message: '请输入理赔说明',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            return true;
        },
        date (time, n) {
            let y, m, d, date, data;
            date = new Date(time);
            y = date.getFullYear();
            m = date.getMonth() + 1;
            d = date.getDate();
            if (n) {
                data = y + '年' + format(m) + '月' + format(d) + '日';
                return data;
            }
            data = y + '-' + format(m) + '-' + format(d);
            return data;
            function format (num) {
                if (parseInt(num) < 10) {
                    num = '0' + num;
                }
                return num;
            }
        }
    }
};
</script>
<style lang="scss" scoped>
  .mint-switch {
      float: right;
  }
  .mint-navbar .mint-tab-item.is-selected {
    border-bottom: none;
    background: #26a2ff;
    margin-bottom: 0;
    color: #fff;
  }
  .mint-navbar .mint-tab-item {
    padding: 10px 0;
  }
  .mint-navbar {
    border-radius: 8px;
  }
  .activity {
    .box {
      position: relative;
      border-radius: 8px;
      color: #666;
      .item {
        border-top-left-radius: 0;
        border-top-right-radius:0;
        border-bottom: 0;
      }
      .right {
        position: absolute;
        right: 1.6rem;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  .my-isSwitchTrue {
    border-radius: 0 !important;
    :-ms-input-placeholder {
      font-size: 14px;
    }
    ::-webkit-input-placeholder {
      font-size: 14px;
    }
  }
  .claims-on-line-2 .form .isSwitchTrue .item {
    .wrap {
      padding: 0rem 1.6rem;
      height: 41px;
      line-height: 1;
    }
  }
</style>



