<template>
  <section class="insure-origin insure-originList">
      <mt-loadmore v-if="list.length > 0" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
        <section class="list">
          <ul>
            <li class="row" @click="jump(item)" v-for="(item, index) in list" :key="index">
              <div class="col-sm col-sm-3 l">
                <img :src="item.insuranceProductImg" alt="">
              </div>
              <div class="col-sm col-sm-9 lc-r">
                <div class="t">{{item.insuranceProductName}}</div>
                <div class="b">
                  <div>{{item.insuranceProductDemo}}</div>
                  <div class="item row">
                      <div class="col-sm col-sm-6 m-l">已投保：{{item.used}}</div>
                      <div class="col-sm col-sm-6 m-r">投保失败：{{item.failure}}</div>
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </section>
      </mt-loadmore>
      <div class="error" v-else>
        <img src="../../assets/images/license-error.png" alt="">
        <p>暂无数据</p>
      </div>
  </section>
</template>

<script>
export default {
    data () {
        return {
            form: {
                pageNumber: 1,
                pageSize: 10
            },
            queryInfo: {},
            list: [],
            allLoaded: false,
            bottomText: '上拉加载更多...'
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            // 线上
            this.form['orgThirdAccount'] = localStorage.getItem('orgThirdAccount');
            // 本地
            // this.form['orgThirdAccount'] = 'BBA101421';
            this.$http.post(this.$api.insurance.queryInsuranceOrgByOrgThirdAccount, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        jump (item) {
            this.$router.push({name: 'insured', query: item});
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.insurance.queryInsuranceOrgByOrgThirdAccount, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        }
    }
};
</script>



