export default {
    minute2hour: function (time) {
        let hour;
        hour = (time % 60 === 0) ? time / 60 : (time / 60).toFixed(1);
        return hour;
    },
    cutstring: function (str, len) {
        let newstr;
        newstr = str.length > len ? (str.substring(0, len) + ' ...') : str;
        return newstr;
    },
    // 状态-10投保失败，10审核中，20保障中
    status: function (type) {
        if (!type) {
            return '';
        }
        const val = parseInt(type);
        if (val === 10) {
            return '审核中';
        }
        if (val === 20) {
            return '保障中';
        }
        if (val === -10) {
            return '投保失败';
        }
        if (val === -20) {
            return '已过期';
        }
        return '';
    },
    // 状态-10投保失败，10审核中，20保障中
    policyStatus: function (type) {
        if (!type) {
            return '';
        }
        const val = parseInt(type);
        if (val === 10) {
            return '待激活';
        }
        if (val === 20) {
            return '保障中';
        }
        if (val === -10) {
            return '投保失败';
        }
        if (val === -20) {
            return '已过期';
        }
        return '';
    },
    // 理赔状态（-20拒绝， -10未通过， 10受理中， 20已理赔）
    queryStatus: function (type) {
        if (!type) {
            return '';
        }
        const val = parseInt(type);
        if (val === 10) {
            return '受理中';
        }
        if (val === 20) {
            return '已理赔';
        }
        if (val === -10) {
            return '未通过';
        }
        if (val === -20) {
            return '拒绝';
        }
        return '';
    },
    // 10个人，20机构，30应用
    from: function (type) {
        if (!type) {
            return '';
        }
        const val = parseInt(type);
        if (val === 10) {
            return '个人';
        }
        if (val === 20) {
            return '机构';
        }
        if (val === 30) {
            return '应用';
        }
        return '';
    },
    date: function (time, hour) { // 把时间戳格式:若有参返回年-月-日时:分:秒 若无参返回 年-月-日
        if (!time) {
            return '';
        }
        let y, m, d, date, h, ms, s, data, hours;
        date = new Date(time);
        y = date.getFullYear();
        m = date.getMonth() + 1;
        d = date.getDate();
        h = date.getHours();
        ms = date.getMinutes();
        s = date.getSeconds();
        data = y + '年' + format(m) + '月' + format(d) + '日';
        hours = format(h) + ':' + format(ms) + ':' + format(s);
        if (hour === 'b') {
            return data.substring(5, 11);
        }
        if (hour) {
            if (hour === 'd') {
                return (data + ' ' + hours).substring(11, 17);
            }
            return data + ' ' + hours;
        } else {
            return data;
        }
        function format (num) {
            if (parseInt(num) < 10) {
                num = '0' + num;
            }
            return num;
        }
    },
    amount (v) {
        v = Number(v);
        if (v > 9999) {
            return v / 10000 + '万';
        }
        return v + '元';
    }
};



