<template>
  <section class="mui-content policy-insuranceDetails">
    <div class="base">
        <div class="title">{{form.insuranceProductName}}</div>
        <div class="item-1 row">
          <div class="col-sm col-sm-8 l">{{form.insurancePersonalNo}}</div>
          <div class="col-sm col-sm-4 r active-1" v-if="form.status === 20">{{form.status | status}}</div>
          <div class="col-sm col-sm-4 r active-2" v-if="form.status === 10">{{form.status | status}}</div>
          <div class="col-sm col-sm-4 r active-4" v-if="form.status === -10">{{form.status | status}}</div>
        </div>
        <div class="item-1 row">
          <div class="col-sm col-sm-8 l">{{form.insuranceCompanyName}}</div>
          <div class="col-sm col-sm-4 r"></div>
        </div>
    </div>
    <div class="line"></div>
    <div class="info">
      <div class="lc-tip row" @click="jump(1)">
        <div class="col-sm col-sm-8 l">
          <div class="block"></div>
          <span>保障计划</span>
        </div>
        <div class="col-sm col-sm-4 r">查看详情 <i class="iconfont icon-arrow-right"></i></div>
      </div>
      <div class="item row" v-for="(item, index) in list" :key="index">
        <div class="col-sm col-sm-4 l">{{item.name}}</div>
        <div class="col-sm col-sm-8 r">{{item.amount | amount}}</div>
      </div>
      <div class="item row">
        <div class="col-sm col-sm-4 l">生效日期</div>
        <div class="col-sm col-sm-8 r">{{form.periodStart | date}}</div>
      </div>
      <div class="item row">
        <div class="col-sm col-sm-4 l">终止日期</div>
        <div class="col-sm col-sm-8 r">{{form.periodEnd | date}}</div>
      </div>
      <div class="item row">
        <div class="col-sm col-sm-4 l">实付保费</div>
        <div class="col-sm col-sm-8 r">
          <span v-if="form.payAmount !== 0">{{form.payAmount}}</span>
          <span v-else>免费</span>
        </div>
      </div>
    </div>
    <div class="line"></div>
    <div class="info-1">
        <div class="item row">
          <div class="col-sm col-sm-4 l font">被保人</div>
          <div class="col-sm col-sm-8 r">{{form.realName}}</div>
        </div>
        <div class="item row">
          <div class="col-sm col-sm-4 l">{{form.cardType}}</div>
          <div class="col-sm col-sm-8 r">{{form.cardNo}}</div>
        </div>
        <div class="item row">
          <div class="col-sm col-sm-4 l">联系电话</div>
          <div class="col-sm col-sm-8 r">{{form.mobile}}</div>
        </div>
    </div>
    <div class="info-2 row">
      <div class="col-sm col-sm-11 l">
        <div class="item row" @click="jump(2)">
          <div class="col-sm col-sm-4 l font">紧急联系人</div>
          <div class="col-sm col-sm-8 r">{{form.urgentName}}</div>
        </div>
        <div class="item row">
          <div class="col-sm col-sm-4 l">与被保人关系</div>
          <div class="col-sm col-sm-8 r">{{form.urgentBind}}</div>
        </div>
        <div class="item row">
          <div class="col-sm col-sm-4 l">联系电话</div>
          <div class="col-sm col-sm-8 r">{{form.urgentMobile}}</div>
        </div>
      </div>
      <div class="col-sm col-sm-1 r" v-if="!form.urgentName">
          <i class="iconfont icon-arrow-right"></i>
      </div>
    </div>
    <div class="line"></div>
    <!-- 保障中的保险 显示理赔记录和理赔按钮 -->
    <div class="info-3">
      <div class="item row" @click="goto(2)" v-if="form.status === 20">
        <div class="col-sm col-sm-8 l font">理赔记录</div>
        <div class="col-sm col-sm-4 r">
          <i class="iconfont icon-arrow-right"></i>
        </div>
      </div>
      <div class="wrap">
        <a :href="form.insuranceProductClause" target="_blank">保险条款</a>
      </div>
    </div>
    <div class="button-wrap">
      <button class="submit" @click="imClaims" v-if="form.status === 20">我要理赔</button>
      <p>如有任何疑问，请联系客服</p>
    </div>
  </section>
</template>

<script>
export default {
    data () {
        return {
            id: null,
            list: [],
            form: {},
            insuranceNo: null
        };
    },
    created () {
        this.$route.query.type === 'query' ? this.initQuery() : this.init();
    },
    methods: {
        init () {
            this.id = this.$route.query.id;
            this.$http.post(this.$api.insurance.queryInsurancePersonalDetailById, {id: this.id}).then(res => {
                if (res.data.code === 0) {
                    this.form = res.data.data;
                    this.list = this.form.list;
                }
            });
        },
        initQuery () {
            this.insuranceNo = this.$route.query.insuranceNo;
            this.$http.post(this.$api.insurance.queryInsurancePersonalDetailByInsuranceNo, {insuranceNo: this.insuranceNo}).then(res => {
                if (res.data.code === 0) {
                    this.form = res.data.data;
                    this.list = this.form.list;
                }
            });
        },
        // 1 保障内容 2 紧急联系人
        jump (type) {
            if (type === 1) {
                this.$router.push({name: 'planDetails', query: this.form});
            } else {
                this.$router.push({name: 'urgency', query: {id: this.id}});
            }
        },
        // 理赔查询
        goto (type) {
            this.$router.push({name: 'query'});
        },
        // 我要理赔
        imClaims () {
            // claimStatus -10可以理赔 10不可理赔
            this.$router.push({name: 'beforeApplying', query: this.form});
        }
    }
};
</script>
<style lang="scss" scoped>
    .active-1 {
      color: #72b81e!important;
    }
    .active-2 {
      color: #f59100!important;
    }
    .active-3 {
      color: #999999!important;
    }
    .active-4 {
      color: #ff0000!important;
    }
</style>



