<template>
    <section class="insuranceIntro">
        <!-- 保险机构详情 -->
        <img class="head-img" :src="form.imgs" alt="">
        <div class="safePlan">
            <div class="row J">
                <span class="col-sm col-sm-8 ">
                    <div class="lc-tip">
                        <span class="block"></span>
                        <span>保障计划</span>
                    </div>
                </span>
                <span class="col-sm col-sm-4 r" @click="jump(1)">查看详情
                  <i class="iconfont icon-arrow-right"></i>
                </span>
            </div>
            <div class="listL">
                <ul>
                    <li class="row" v-for="(item, index) in list" :key="index">
                        <div class="col-sm col-sm-6 Tl">{{item.name}}</div>
                        <div class="col-sm col-sm-6 Tr">{{item.amount | amount}}</div>
                    </li>
                    <li class="row">
                        <div class="col-sm col-sm-6 Tl">投保年龄</div>
                        <div class="col-sm col-sm-6 Tr">{{form.insuranceMinAge}}-{{form.insuranceMaxAge}}
                          <span v-if="form.reInsuranceAge !== 0">(可续保至{{form.reInsuranceAge}})</span>
                          <span v-else>(不可续保)</span>
                          </div>
                    </li>
                    <li class="row">
                        <div class="col-sm col-sm-6 Tl">保障期限</div>
                        <div class="col-sm col-sm-6 Tr">
                          <span v-if="form.validityPeriod === 0">永久</span>
                          <span v-else>{{form.validityPeriod}}年</span>
                        </div>
                    </li>
                    <li class="row">
                        <div class="col-sm col-sm-6 Tl">保费</div>
                        <div class="col-sm col-sm-6 Tr">{{form.insuranceAmount}}积分</div>
                    </li>
                </ul>
            </div>
        </div>

        <div class="safePlan">
            <div class="row J">
                <span class="col-sm col-sm-8 ">
                    <div class="lc-tip">
                        <span class="block"></span>
                        <span>理赔说明</span>
                    </div>
                </span>
                <span class="col-sm col-sm-4 r" @click="jump(2)">理赔须知<i class="iconfont icon-arrow-right"></i></span>
            </div>
            <div class="listL">
                <ul>
                    <li class="row">
                        <div class=" Tl"><a :href="'tel:' + form.claimMobile">拨打理赔电话</a></div>
                    </li>
                    <li class="row">
                        <div class=" Tl">准备理赔材料</div>
                    </li>
                    <li class="row">
                        <div class=" Tl">完成理赔</div>
                    </li>
                </ul>
            </div>
        </div>
        <div class="safePlan">
            <div class="row J" @click="goto">
                <span class="col-sm col-sm-8 ">
                    <div class="lc-tip">
                        <span class="block"></span>
                        <span>购买须知</span>
                    </div>
                </span>
                <span class="col-sm col-sm-4 r">
                  <i class="iconfont icon-jay"></i>
                  <em class="jay">{{form.insuranceAmount}}积分/份</em>
                </span>
            </div>
            <div class="listL">
                <ul>
                    <!-- <li class="row">
                        <div class="col-sm col-sm-6 Tl">投保数量</div>
                        <div class="col-sm col-sm-6 Tr">
                          请选择
                          <i class="iconfont icon-arrow-right"></i>
                        </div>
                    </li> -->
                    <li class="row">
                        <div class="col-sm col-sm-6 Tl">账户剩余积分</div>
                        <div class="col-sm col-sm-6 Tr">{{form.freeAmount}}积分</div>
                    </li>
                </ul>
            </div>
        </div>
        <div class="introBtn">
            <button class="submit-btn-1" @click="submit">捐赠</button>
        </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            form: {},
            list: [],
            id: null,
            num: null,
            prjvOrgId: null
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            // 线上
            let storage = localStorage.getItem('appUserInfo');
            if (storage) {
                storage = JSON.parse(storage);
                this.prjvOrgId = storage.prjvOrgId;
            }
            this.id = this.$route.query.id;
            this.$http.post(this.$api.insurance.queryInsuranceProductDetailById, {insuranceProductId: this.id, prjvOrgId: this.prjvOrgId}).then(res => {
                if (res.data.code === 0) {
                    this.form = res.data.data;
                    this.list = res.data.data.list;
                }
            });
        },
        goto () {
            this.$router.push({name: 'noticeInsure', query: {type: '1'}});
        },
        jump (type) {
            type === 1 ? this.$router.push({name: 'planDetails', query: this.form}) : this.$router.push({name: 'noticeInsure'});
        },
        submit () {
            if (this.form.freeAmount < this.form.insuranceAmount) {
                this.$mint.Toast({
                    message: '当前积分不足',
                    position: 'top',
                    duration: 1000
                });
                return;
            }
            this.$route.query['insuranceProductId'] = this.form.id;
            this.$router.push({name: 'selectUser', query: this.$route.query});
            // console.log(this.$route.query);
        }
    }
};
</script>
<style lang="scss" scoped>
  .head-img {
    width: 100%;
    height: px2rem(100);
    min-height: px2rem(100);
  }
  .jay {
    vertical-align: middle;
    margin-left: px2rem(-5);
  }
  .listL {
    a {
      text-decoration: none;
      color: #000;
    }
  }
</style>



