<template>
    <section class="insure-insured insure-origin memo">
      <header :style="{backgroundImage: 'url('+note+')'}"></header>
      <section class="list" v-if="list && list.length > 0">
        <ul>
            <li v-for="(item, index) in list" :key="index" @click="goto(item)">
                <div class="item row">
                  <div class="col-sm col-sm-3 l">
                      <i class="iconfont icon-clock-blod"></i>
                      <span>时间：</span>
                  </div>
                  <div class="col-sm col-sm-9 last">{{item.createTime}}</div>
                </div>
                <div class="item row">
                  <div class="col-sm col-sm-3 l">
                      <i class="iconfont icon-clock-blod"></i>
                      <span>地点：</span>
                  </div>
                  <div class="col-sm col-sm-9 last">{{item.address}}</div>
                </div>
                <div class="item row">
                  <div class="col-sm col-sm-3 l">
                      <i class="iconfont icon-explain"></i>
                      <span>说明：</span>
                  </div>
                  <div class="col-sm col-sm-9 last">{{item.content}}</div>
                </div>
            </li>
        </ul>
      </section>
      <div class="error" v-else>
        <img src="../../assets/images/license-error.png" alt="">
        <p>暂无随手签数据</p>
      </div>
    </section>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/publicity-header.png'),
            form: {
                months: 6,
                userCode: null
            },
            list: [
                // {
                //     id: 13,
                //     applicationId: 'GYSH',
                //     uuid: '98c6f960cf464e3fb56953a0710de144',
                //     lat: '',
                //     lng: '',
                //     address: '上海市',
                //     locationType: 'GPS',
                //     userCode: '72-00001669-2',
                //     contentId: '',
                //     content: 365,
                //     refSource: '',
                //     isDelete: 0,
                //     createTime: '2018-12-12 09:46:15',
                //     isSync: 0,
                //     syncTime: '',
                //     syncEntry: ''
                // },
                // {
                //     id: 13,
                //     applicationId: 'GYSH',
                //     uuid: '98c6f960cf464e3fb56953a0710de144',
                //     lat: '',
                //     lng: '',
                //     address: '上海市',
                //     locationType: 'GPS',
                //     userCode: '72-00001669-2',
                //     contentId: '',
                //     content: 365,
                //     refSource: '',
                //     isDelete: 0,
                //     createTime: '2018-12-12 09:46:15',
                //     isSync: 0,
                //     syncTime: '',
                //     syncEntry: ''
                // }
            ]
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.form.userCode = pfa);
            this.$http.post(this.$api.insurance.memo, this.form).then(res => {
                if (res.data.code === 0) {
                    this.list = res.data.data;
                }
            });
        },
        goto (item) {
            localStorage.setItem('memoForm', JSON.stringify(item));
            this.$router.push({name: 'information', query: {type: 'click'}});
        }
    }
};
</script>
<style lang="scss" scoped>
  .search {
    padding: px2rem(15);
    position: relative;
    width: 100%;
    input {
      width: 100%;
      border-radius: 35px;
      padding-left: px2rem(10);
      height: px2rem(35);
      border: 1px solid #d5d5d5;
      padding-right: 28px;
      background-color: rgba(255, 255, 255, 0.3);
    }
    i {
      position: absolute;
      right: px2rem(25);
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .icon-explain, .icon-clock-blod, .icon-clock-blod {
    color: $color-highlight;
  }
  .icon-explain {
    font-size: 16px;
    margin-left: 2px;
  }
  .icon-clock-blod, .icon-clock-blod {
    font-size: 18px;
  }
  .list {
    position: relative;
    top: px2rem(-50);
  }
  ul {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
  }
  li {
    .l {
      .iconfont, span {
        vertical-align: middle;
        display: inline-block;
      }
    }
    .last {
      color: $color-minor;
      vertical-align: middle;
    }
    border-radius: 8px !important;
  }
</style>



